/*========== Basic ==========*/
html, body {
  min-width: var(--screen-min-width);
}

body {
  background-color: var(--color-background-4);
  padding: 0 !important;
  overflow-y: scroll;
  overflow-x: hidden;
}
body::-webkit-scrollbar-track {
  background-color: var(--color-body-scrollbar);
}

* {
  box-sizing: border-box;
  min-height: 0px;
  min-width: 0px;
  -webkit-touch-callout: none;
  -webkit-user-drag: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}
*::-webkit-scrollbar {
  width: var(--custom-scrollbar-width);
}
*::-webkit-scrollbar-track {
  background-color: var(--color-scrollbar);
}
*::-webkit-scrollbar-thumb {
  background-color: var(--color-scrollbar-thumb);
}
*:before, *:after {
  box-sizing: border-box;
}

button, input, textarea, select {
  outline: none;
  display: block;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  appearance: none;
  padding: 0;
  border-radius: 0;
  color: inherit;
  font-weight: inherit;
  text-rendering: inherit;
  line-height: inherit;
}

a, button, label, [data-touch-hover] {
  user-select: none;
  -webkit-user-drag: none;
  cursor: pointer;
}
a *, button *, label *, [data-touch-hover] * {
  user-select: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s;
}

textarea {
  resize: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

table {
  border-collapse: collapse;
}

a {
  text-decoration: none;
  color: inherit;
}

/*========== Layout ==========*/
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: var(--screen-height);
  width: var(--content-width);
  overflow: hidden;
  position: relative;
}

.main {
  --main-top-padding: 150px;
  flex: 1 1 100%;
  padding-top: var(--main-top-padding);
}
@media (max-width: 1023.98px) {
  .main {
    --main-top-padding: 110px;
  }
}
@media (max-width: 575.98px) {
  .main {
    --main-top-padding: 95px;
  }
}

.section {
  margin-bottom: var(--indent-1);
}
.section:last-child {
  margin-bottom: calc(var(--indent-1) / 2);
}
.section__head {
  margin-bottom: 40px;
}
@media (max-width: 1023.98px) {
  .section__head {
    margin-bottom: 35px;
  }
}
@media (max-width: 575.98px) {
  .section__head {
    margin-bottom: 30px;
  }
}

.section-border {
  margin-top: calc((var(--indent-1) - var(--indent-3)) * -1);
}
.section-border__head {
  margin-bottom: 30px;
}
.section-border > .container::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--color-background-2);
  margin-bottom: 30px;
}
@media (max-width: 1023.98px) {
  .section-border__head {
    margin-bottom: 25px;
  }
  .section-border > .container::before {
    margin-bottom: 25px;
  }
}

.container {
  position: relative;
}

.scrollbar {
  --overscroll-size: 20px;
  --overscroll-bg-1: var(--scroll-start-gradient-1);
  --overscroll-bg-2: var(--scroll-end-gradient-1);
  position: relative;
  z-index: 1;
  overflow-y: auto;
  overflow-x: hidden;
}
.scrollbar::before, .scrollbar::after {
  content: "";
  display: block;
  width: 100%;
  height: var(--overscroll-size);
  position: sticky;
  left: 0;
  z-index: 2;
  pointer-events: none;
}
.scrollbar::before {
  top: 0;
  background: linear-gradient(0deg, var(--overscroll-bg-2) 0%, var(--overscroll-bg-1) 100%);
}
.scrollbar::after {
  bottom: 0;
  background: linear-gradient(0deg, var(--overscroll-bg-1) 0%, var(--overscroll-bg-2) 100%);
}

/*========== Components ==========*/
img {
  display: block;
  user-select: none;
  pointer-events: none;
}
img.lazyload, img.lazyloading {
  opacity: 0;
  visibility: hidden;
}
img.lazyloaded {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--trs-3), visibility var(--trs-3);
}

.image {
  display: block;
  position: relative;
  -webkit-transform: translateZ(0);
}
.image img {
  width: 100%;
  height: auto;
}
.image_contain img, .image_cover img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.image_cover {
  overflow: hidden;
}
.image_cover img {
  object-fit: cover;
}
.image_contain img {
  object-fit: contain;
}

.image-content {
  background-color: var(--color-background-3);
  --size: var(--image-size);
  --image-size: 100%;
  --image-sm-size: var(--image-size);
}
.image-content::before {
  content: "";
  display: block;
  padding-top: var(--size);
}
@media (min-width: 576px) {
  .image-content {
    --size: var(--image-sm-size);
  }
}
.image-content_left {
  width: calc(100% + var(--content-gutter));
  margin-left: calc(var(--content-gutter) * -1);
}
.image-content_right {
  width: calc(100% + var(--content-gutter));
}

.icon {
  display: block;
  width: 20px;
  height: 20px;
}

.close-button {
  padding: 10px;
  margin: -10px;
  color: var(--color-close);
  transition: color var(--trs-1);
  position: absolute;
  top: var(--close-btn-top);
  right: var(--close-btn-right);
  z-index: 2;
}
.close-button .icon {
  width: var(--close-btn-size);
  height: var(--close-btn-size);
}
.close-button[data-touch], .close-button[data-hover] {
  color: var(--color-close-hover);
}

.tippy-box {
  border-radius: 5px;
  background-color: var(--color-tippy-background);
  color: var(--color-tippy);
  padding: 8px 14px;
  transition-timing-function: ease-in-out;
  box-shadow: var(--modal-shadow);
}
.tippy-box[data-placement=bottom] {
  margin-top: 25px;
}
.tippy-box[data-animation=fade][data-state=hidden] {
  opacity: 0;
}
.tippy-box[data-animation=fade][data-state=visible] {
  opacity: 1;
}

@media (min-width: 1024px) {
  [data-3d=parent] {
    position: relative;
    transform: translateZ(1000px);
    transform-style: preserve-3d;
  }

  [data-3d=back], [data-3d=forward] {
    position: relative;
  }

  [data-3d=back] {
    z-index: 1;
    transform: perspective(500px);
  }

  [data-3d=forward] {
    z-index: 2;
    transform: perspective(500px) translateZ(50px) scale(0.9);
  }
}
.link-arrow {
  display: flex;
  align-items: center;
}
.link-arrow .icon {
  width: 1em;
  height: 1em;
  margin-left: 1em;
}
.link-arrow[data-touch], .link-arrow[data-hover] {
  color: var(--color-text-2);
}

.dev-label {
  font-size: 0.875rem;
}
.dev-label span {
  color: var(--color-text-4);
}
.dev-label a {
  display: inline-block;
  transform: translateY(7%);
}
.dev-label a span {
  display: none;
}
.dev-label svg {
  width: 5.5em;
  display: block;
}

.point-text {
  display: inline-flex;
  align-items: center;
}
.point-text .icon {
  width: 1em;
  height: 1em;
  margin-right: 0.5em;
}

.timetable-element {
  --height: 7px;
  --margin: 20px;
}
.timetable-element__days {
  display: flex;
  margin-bottom: var(--margin);
}
.timetable-element__day {
  flex: 1 1 14.28%;
