/*========== Nav ==========*/
.mobile-nav {
  --font-size: 20px;
  --line-height: 24px;
  position: fixed;
  height: 100%;
  width: var(--screen-width);
  top: 0;
  left: 0;
  z-index: 1001;
  display: none;
}
.mobile-nav__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--color-overlay);
}
.mobile-nav_visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.mobile-nav_visible .mobile-nav__container {
  transform: translate(0);
}
.mobile-nav__container {
  width: 400px;
  height: 100%;
  background-color: var(--color-background-1);
  position: relative;
  z-index: 2;
  transition: transform var(--trs-1);
}
.mobile-nav__head {
  position: sticky;
  z-index: 2;
  top: 0;
  margin-top: calc(var(--overscroll-size) * -1);
  height: var(--indent-3);
}
.mobile-nav__content {
  padding-bottom: calc(var(--indent-3) - var(--overscroll-size));
}
.mobile-nav__item:not(:last-child) {
  margin-bottom: 22px;
}
.mobile-nav__link {
  display: block;
  font-size: var(--font-size);
  line-height: var(--line-height);
  color: var(--color-text-2);
  font-weight: 700;
  padding: 0 50px;
}
.mobile-nav__categories-list {
  padding-bottom: 12px;
  margin-top: 22px;
}
.mobile-nav__categories-link {
  display: block;
  position: relative;
  padding-left: 50px;
  font-size: var(--font-size);
  line-height: var(--line-height);
  padding: 10px 50px 10px 100px;
  transition: background-color var(--trs-1), color var(--trs-1);
}
.mobile-nav__categories-link-icon {
  display: block;
  position: absolute;
  left: 50px;
  top: calc(10px + (var(--line-height) - var(--height)) / 2);
  width: 40px;
  height: var(--height);
}
.mobile-nav__categories-link-icon_1 {
  --height: 14px;
}
.mobile-nav__categories-link-icon_2 {
  --height: 20px;
}
.mobile-nav__categories-link-icon_3 {
  --height: 14px;
}
.mobile-nav__categories-link-icon_4 {
  --height: 32px;
}
.mobile-nav__categories-link-icon_5 {
  --height: 24px;
}
.mobile-nav__categories-link-icon_6 {
  --height: 30px;
}
.mobile-nav__categories-link-icon_7 {
  --height: 7px;
}
.mobile-nav__categories-link-icon_8 {
  --height: 26px;
}
.mobile-nav__categories-link-icon_9 {
  --height: 19px;
}
.mobile-nav__categories-link-icon_10 {
  --height: 35px;
}
@media (min-width: 576px) {
  .mobile-nav__logo {
    display: none;
  }
}
@media (min-width: 1024px) {
  .mobile-nav__container {
    animation: var(--trs-1) mobile-nav-slide forwards paused;
  }
  .d-block .mobile-nav__container {
    animation-play-state: running;
  }
  .mobile-nav__content .container {
    padding: 0;
