}
/*========== Search ==========*/
.search-element {
  --search-height: 50px;
}
.search-element__form {
  position: relative;
  z-index: 2;
  border-radius: calc(var(--search-height) / 2);
  background-color: var(--color-search-bg);
  transition: background-color var(--trs-1);
}
.search-element__form .icon {
  position: absolute;
  width: 18px;
  height: 18px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--color-text-4);
  transition: color var(--trs-1);
  pointer-events: none;
}
.search-element__form[data-touch], .search-element__form[data-hover] {
  background-color: var(--color-search-bg-hover);
}
.search-element__input {
  font-size: 16px;
  line-height: 20px;
  padding: calc((var(--search-height) - 20px) / 2) 50px calc((var(--search-height) - 20px) / 2) 20px;
  width: 100%;
  border-radius: inherit;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--color-text-5);
}
.search-element__input:-webkit-autofill {
  -webkit-text-fill-color: var(--color-text-5);
}
.search-element__input::placeholder {
  color: var(--color-text-4);
  transition: opacity var(--trs-1);
}
.search-element__input:focus::placeholder {
  opacity: 0;
}
.search-element__resault-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
