/*========== Location ==========*/
.location-element {
  position: absolute;
  z-index: 1001;
  background-color: var(--color-background-1);
  border-radius: var(--select-list-radius);
  overflow: hidden;
  width: 220px;
  box-shadow: var(--modal-shadow);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity var(--trs-1), visibility var(--trs-1);
}
.location-element_visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.location-element__container {
  --overscroll-size: var(--select-py);
  max-height: var(--select-max-height);
  overflow: auto;
}
.location-element__link {
  display: block;
  transition: color var(--trs-1), background-color var(--trs-1);
  padding: var(--select-item-py) var(--select-item-px);
  line-height: var(--select-item-lh);
}
.location-element__link[data-touch], .location-element__link[data-hover] {
  background-color: var(--color-background-2);
}
