/*========== Footer ==========*/
.footer {
  --gx: 100px;
  --gy: 25px;
  flex-shrink: 0;
  position: relative;
  padding-top: var(--angle-border-size);
}
.footer:before, .footer:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  background-color: var(--color-background-3);
}
.footer:before {
  height: var(--angle-border-size);
  clip-path: polygon(0 0, 0% 100%, 100% 100%);
  top: 0;
}
.footer::after {
  top: var(--angle-border-size);
  height: calc(100% - var(--angle-border-size));
}
.footer__container {
  padding-top: 25px;
  padding-bottom: 25px;
  position: relative;
  z-index: 2;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  gap: 20px var(--gx);
}
.footer__socials {
  flex: 0 0 auto;
  display: flex;
  justify-content: flex-end;
}
.footer__socials:first-child {
  flex: 1 0 auto;
}
.footer__nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex: 1 1 auto;
  gap: var(--gy) var(--gx);
}
.footer__nav-link {
  display: flex;
  align-items: center;
}
.footer__nav-link .icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.65em;
}
.footer__bottom {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: var(--gy) var(--gx);
}
.footer__copyright {
  color: var(--color-text-6);
}
@media (max-width: 1599.98px) {
  .footer {
    --gx: 80px;
  }
}
@media (max-width: 1023.98px) {
  .footer__container {
    padding-top: 30px;
  }
  .footer__bottom {
    margin-top: 50px;
  }
  .footer__nav {
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (max-width: 575.98px) {
  .footer {
    text-align: center;
  }
  .footer__container {
    padding-top: 45px;
  }
  .footer__top {
    flex-direction: column;
    align-items: center;
    gap: 60px 0;
  }
